import React, { Component } from 'react';
import './App.css';
import Jumbotron from "./components/Jumbotron";
import Brands from "./components/Brands";
import Models from "./components/Models";
import Engines from "./components/Engines";
import Packages from "./components/Packages";
import Offers from "./components/Offers";
import Email from "./components/Email";
import HowItWorks from "./components/HowItWorks";
import Guides from "./components/Guides";
import Footer from "./components/Footer";
import Header from "./components/Header";

class App extends Component {
  constructor(props) {
    super(props);
    this.change = this.change.bind(this);
    this.state = {
      step: 'default'
    };
  }

  change(step) {
    this.setState({
      step: step
    });
  }

  render() {
    const steps = {
      brands: <Brands next={() => this.change('models')}/>,
      models: <Models next={() => this.change('engines')}/>,
      engines: <Engines next={() => this.change('packages')}/>,
      packages: <Packages next={() => this.change('email')}/>,
      email: <Email next={() => this.change('email-given')} email={null} />,
      "email-given": <Email next={() => this.change('offers')} email="marcin.kowalski@example.com" />,
      offers: <Offers next={() => this.change('contacted')}/>,
      contacted: <Offers next={() => this.change('contacted')}/>,
      default: <div><Jumbotron next={() => this.change('brands')}/><HowItWorks /><Guides/></div>
    };
    const content = <div><Header/>{steps[this.state.step]}<Footer/></div>;
    return (content);
    // switch (this.state.step) {
    //   case 'brands':
    //     return (<Brands next={() => this.change('models')}/>);
    //   case 'models':
    //     return (<Models next={() => this.change('engines')}/>);
    //   case 'engines':
    //     return (<Engines next={() => this.change('packages')}/>);
    //   case 'packages':
    //     return (<Packages next={() => this.change('email')}/>);
    //   case 'email':
    //     return (<Email next={() => this.change('email-given')} email={null} />);
    //   case 'email-given':
    //     return (<Email next={() => this.change('offers')} email={"marcin.kowalski@example.com"} />);
    //   case 'offers':
    //     return (<Offers next={() => this.change('contacted')}/>);
    //   case 'contacted':
    //     alert('Twoje dane kontaktowe zostaly przekazane do dealera');
    //     return (<Offers next={() => this.change('contacted')}/>);
    //   default:
    //     return (<Jumbotron next={() => this.change('brands')}/>);
    // }

  }
}



export default App;
