import React, { Component } from 'react';
import bmwx3 from './bmw_x3.png';

class Models extends Component {
    constructor(props){
        super(props);
        this.state = {
            models: Array(10).fill(
                {
                    name: 'X3',
                    image: bmwx3
                }
            )
        }
    }

    render() {

        const models = this.state.models.map((model) => {
            return <div className="col-sm-2 tile tile--brand" onClick={() => this.props.next()}><img src={model.image} style={{width: '150px'}} />{model.name}</div>;
        });

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <h1>Wybierz model</h1>
                    <div className="row tile--row">
                        {models}
                    </div>
                </div>
            </section>
        )
    }
}

export default Models;