import React, { Component } from 'react';
import merc from './img/mercedes-benz.jpg';

export default class Guides extends Component {
    render() {
        return (
            <section className="guides p-3">
                <div className="container">
                    <div className="row">
                        <h2>Skorzystaj z naszych poradników by podjąć najlepsza decyzję!</h2>
                    </div>
                    <div className="row">
                        <div className="col-sm">
                            <img src={merc} width="350" alt="" />
                            <h3>Sprawdź nasze opinie</h3>
                            <p>Przedstawiamy wam kompleksowe przeglądy aut, abyście niczego nie przeoczyli.</p>
                        </div>
                        <div className="col-sm">
                            <img src={merc} width="350" alt="" />
                            <h3>Zobacz najpopularniejsze modele</h3>
                            <p>Dowiedz się, które marki są aktualnie najpopularniejsze i które modele sprzedają się najlepiej</p>
                        </div>
                        <div className="col-sm">
                            <img src={merc} width="350" alt="" />
                            <h3>Kupuj pewnie i świadomie!</h3>
                            <p>Skorzystaj z naszych poradników, aby kupować pewnie i świadomie - zależy nam na waszej satysfakcji.</p>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
