import React, { Component } from 'react';
import bmw from './bmw.svg';
// import { ReactComponent as BmwLogo } from './bmw.svg';

class Brands extends Component {
    constructor(props){
        super(props);
        this.state = {
            brands: Array(26).fill(
                {
                    name: 'BMW',
                    image: bmw
                }
            )
        }
    }

    render() {

        const brands = this.state.brands.map((brand) => {
            return <div className="col-sm-2 tile tile--brand" onClick={() => this.props.next()}><img src={brand.image} style={{width:'150px'}} />{brand.name}</div>;
        });

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <h1>Wybierz markę</h1>
                    <div className="row tile--row">
                        {brands}
                    </div>
                </div>
            </section>
        )
    }
}

export default Brands;