import React, { Component } from 'react';

class Engines extends Component {
    constructor(props){
        super(props);
        this.state = {
            engines: Array(6).fill(
                {
                    model: '420d 190',
                    fuel: 'Diesel',
                    gearbox: 'Manual',
                    power: '190 KM',
                    effectiveness: '8.5 / 100km',
                    price: '157.349 PLN'
                }
            )
        }
    }

    render() {

        const engines = this.state.engines.map((engine) => {
            return (
                <div className="row border mb-2 py-2">
                    <div className="col-2">{engine.model}</div>
                    <div className="col-1">{engine.fuel}</div>
                    <div className="col-2">{engine.gearbox}</div>
                    <div className="col-2">{engine.power}</div>
                    <div className="col-2">{engine.effectiveness}</div>
                    <div className="col-2">{engine.price}</div>
                    <div className="col-md-1 text-center px-0">
                        <button type="button" className="btn btn-primary" onClick={() => this.props.next()}>Wybierz</button>
                    </div>
                </div>
            )
        });

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <h1>Wybierz silnik</h1>
                    <div className="row mb-2 py-2">
                        <div className="col-2">Silnik</div>
                        <div className="col-1">Typ</div>
                        <div className="col-2">Skrzyna biegów</div>
                        <div className="col-2">Moc</div>
                        <div className="col-2">Spalanie</div>
                        <div className="col-2">Cena</div>
                    </div>
                    <div>
                        {engines}
                    </div>
                </div>
            </section>
        )
    }
}

export default Engines;