import React, { Component } from 'react';

class Jumbotron extends Component {
    render() {
        return (
            <section className="jumbotron">
                <div className="container justify-content-center">
                    <h1 className="jumbotron-heading">Inteligentny sposób kupowania auta!</h1>
                    <p className="lead text-muted">Choose your perfect car, compare offers from local and national dealers and buy at a price that’s right for you.</p>
                    <p>
                        <a href="#" className="btn btn-primary my-2" onClick={() => this.props.next()}>Wybierz auto</a>
                        {/*<a href="#" className="btn btn-secondary my-2">Secondary action</a>*/}
                    </p>
                </div>
            </section>
        )
    }
}

export default Jumbotron;