import React, { Component } from 'react';

class Email extends Component {

    render() {

        const email = 'marcin.kowalski@example.com';
        const form = (<form>
            <p>W tym celu potrzebujemy Twój adres e-mail.</p>
            <div className="form-group">
                <label htmlFor="email">Email</label>
                <input id="email" type="email" className="form-control" value={email} placeholder="Podaj email"/>
                <small id="emailHelp" className="text-muted">
                    Nie przekazujemy nikomu Twojego adresu e-mail.
                </small>
            </div>
            <button className="btn btn-primary float-right" type="submit" onClick={() => this.props.next()}>Wyślij</button>
        </form>);
        const summary = (<div>
            <p>Będziemy informować Cię na bieżąco, o każdej przygotowanej dla Ciebie ofercie mailowo.</p>
            <p>Twoje oferty możesz również sprawdzić klikając poniżej</p>
            <button className="btn btn-primary" onClick={() => this.props.next()}>Sprawdź swoje oferty</button>
        </div>);

        let content = form;
        if (this.props.email) {
            content = summary;
        }

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <div className="row">
                        <div className="col-6 mx-auto justify-content-center">
                            <h1>Dziękujemy za wybranie samochodu</h1>
                            <p>Twoje zapytanie jest w tym momencie przesyłany do współpracujących z nami salonów
                            samochodowych. W ciągu 24 godzin otrzymasz od nas przynajmniej 5 ofert
                            na wybrane przez Ciebie auto.</p>
                            {content}
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Email;