import React, { Component } from 'react';
import bmw from './bmw.svg';

class Offers extends Component {
    constructor(props){
        super(props);
        this.state = {
            offers: Array(1).fill(
                {
                    name: 'BMW M3 SPORT',
                    image: bmw,
                    color: 'Black',
                    price: '197.349 PLN'
                }
            )
        }
    }

    render() {

        const offers = this.state.offers.map((offer) => {
            return (<div className="row border mb-2 py-2">
                <div className="col-9">
                    <h2>{offer.name}</h2>
                </div>
                <div className="col-2">{offer.price}</div>
                <div className="col-md-1 text-center px-0 align-middle">
                    <button type="button" className="btn btn-primary" onClick={() => this.props.next()}>Jazda probna</button>
                </div>
            </div>);
        });

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <h1>Propozycje dealerów</h1>
                    {offers}
                </div>
            </section>
        )
    }
}

export default Offers;