import React, { Component } from 'react';
import bmw from './bmw.svg';

class Packages extends Component {
    constructor(props){
        super(props);
        this.state = {
            packages: Array(3).fill(
                {
                    name: 'Sport',
                    description: 'Sport is the entry-level trim for the BMW 4 Series. As standard you get 18-inch alloy wheels,\n' +
                        'high-gloss exterior plastics and a chrome-trimmed exhaust,\n' +
                        'but it doesn’t look as striking as the M Sport model. Standard kit is pretty generous, though,\n' +
                        'and includes satellite navigation, heated front seats, climate control and ambient\n' +
                        'lighting with three colour settings.',
                    price: '197.349 PLN'
                }
            )
        }
    }

    render() {

        const packages = this.state.packages.map((pckg) => {
            return (<div className="row border mb-2 py-2">
                <div className="col-9">
                    <h2>{pckg.name}</h2>
                    <p>{pckg.description}</p>
                </div>
                <div className="col-2">{pckg.price}</div>
                <div className="col-md-1 text-center px-0 align-middle">
                    <button type="button" className="btn btn-primary" onClick={() => this.props.next()}>Wybierz</button>
                </div>
            </div>);
        });

        return (
            <section className="p-3">
                <div className="container" style={{paddingBottom:"100px"}}>
                    <h1>Wybierz pakiet</h1>
                    {packages}
                </div>
            </section>
        )
    }
}

export default Packages;