import React, { Component } from 'react';
import house from './img/house.svg';
import laptop from './img/laptop.svg';
import speech from './img/speech.svg';

export default class HowItWorks extends Component {
    render() {
        return (
            <section className="promo">
                <div className="container">
                    <div className="row">
                        <h2>Pozwól nam znaleźć auto dla Ciebie! Jak to działa?</h2>
                    </div>
                    <div className="row">
                        <div className="col-sm text-center">
                            <img src={laptop} alt="" />
                            <h3>1. WYBIERZ AUTO MARZEŃ</h3>
                            <p>Skonfiguruj auto, które chcesz kupić, a otrzymasz od nas zestawienie najlepszych ofert.</p>
                        </div>
                        <div className="col-sm text-center">
                            <img src={speech} alt="" />
                            <h3>2. PRZEJRZYJ ZESTAWIENIE OFERT</h3>
                            <p>Przeglądaj oferty od sprawdzonych dealerów i porównaj ich ceny, lokalizacje
                                i ratin - za darmo!</p>
                        </div>
                        <div className="col-sm text-center">
                            <img src={house} alt="" />
                            <h3>3. NAJLEPSZA OFERTA</h3>
                            <p>Kontaktujesz się z dealerem, który przedstawił Ci najlepsza ofertę. Możesz z nim
                                omówić wszelkie szczegóły i dokonać kupna bezpośrednio.</p>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
